.page-numbers:hover {
    font-weight: bold;
    transform: scale(1.1);
}

.post-grid-tag-style-container {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
}

.post-grid-category-style-container {
    display: flex;
    flex-wrap: wrap;
}

.excerpt_selector {
    text-decoration: none;
    position: relative;
    display: inline-block;
}
.th-post-grid-main-wp-editor-wrapper{
    margin-left:auto !important;
    margin-right:auto !important;
}

.excerpt_selector::after {
    content: '';
    position: absolute;
    width: 0;
    height: 1.5px;
    bottom: 0;
    left: 0;
    background-color: currentColor;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

.excerpt_selector:hover::after {
    visibility: visible;
    width: 100%;
}


.post-grid-featured-image{
    overflow: hidden;
}

.animatefeaturedimage-front {
	transition: transform 0.3s ease-in-out;
	overflow: hidden; /* Ensures the zoom doesn't go outside the container */
}
  
.animatefeaturedimage-front:hover {
transform: scale(1.3); /* Slight zoom on hover */
}